import React from 'react';
import { Route, Routes } from 'react-router-dom';

const PaymentMethod = React.lazy(() => import('./PaymentMethod/PaymentMethod'));
const Intrabank = React.lazy(() => import('./Intrabank/Intrabank'));
const Ach = React.lazy(() => import('./Ach/Ach'));
const DomesticWireAddress = React.lazy(() => import('./DomesticWire/DomesticWireAddress'));
const DomesticWirePayment = React.lazy(() => import('./DomesticWire/DomesticWirePayment'));
const InternationalWireAddress = React.lazy(() => import('./InternationalWire/InternationalWireAddress'));
const InternationalWirePayment = React.lazy(() => import('./InternationalWire/InternationalWirePayment'));
const SendCard = React.lazy(() => import('./SendCard/SendCard'));
const Check = React.lazy(() => import('./Check/Check'));
const PaymentSuccess = React.lazy(() => import('./PaymentSuccess/PaymentSuccess'));

const ContactRouter = () => (
  <Routes>
    <Route
      path="types"
      element={(<PaymentMethod />)}
    />
    <Route
      path="intrabank"
      element={(<Intrabank />)}
    />
    <Route
      path="ach"
      element={(<Ach />)}
    />
    <Route
      path="domesticWire/address"
      element={(<DomesticWireAddress />)}
    />
    <Route
      path="domesticWire/payment"
      element={(<DomesticWirePayment />)}
    />
    <Route
      path="internationalWire/address"
      element={(<InternationalWireAddress />)}
    />
    <Route
      path="internationalWire/payment"
      element={(<InternationalWirePayment />)}
    />
    <Route
      path="sendCard"
      element={(<SendCard />)}
    />
    <Route
      path="check"
      element={(<Check />)}
    />

    <Route
      path="success"
      element={(<PaymentSuccess />)}
    />
  </Routes>
);

export default ContactRouter;
